<style lang="less" src="./list.less"></style>

<template>
    <el-dialog title="修改权限" :visible="open" width="800px" :before-close="onClose">
        <el-form ref="form" label-width="90px" size="small">
            <el-form-item label="用户名">
                <el-input v-model="name" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="开通权限">
                <el-checkbox-group v-model="auth">
                    <el-checkbox v-for="(v, k) in authEnums" :key="k" :label="v.value">{{v.text}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose">取 消</el-button>
            <el-button type="primary" @click="onSubmit">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import _ from 'lodash'
    import {ROLE_ENUMS} from '../../../conf/enums'

    export default {
        computed: {
            authEnums: () => ROLE_ENUMS
        },

        data() {
            return {
                open: false,
                callback: null,
                id: '',
                name: '',
                auth: []
            }
        },

        mounted() {

        },

        methods: {
            showDialog({onSuccess, row} = {}) {
                this.open = true
                this.name = row.name
                this.id = row.id
                this.auth = row.auth
                this.callback = onSuccess
            },

            onClose() {
                this.open = false
                this.$refs['form'].resetFields()
            },

            onSubmit() {
                const auth = [...this.auth]
                auth.sort()
                this.$store.dispatch('accountEdit', {
                    data: {
                        id: this.id,
                        auth: auth.join('')
                    },
                    success: () => {
                        this.onClose()
                        if (_.isFunction(this.callback)) {
                            this.callback()
                        }
                    },
                    error: msg => {
                        this.alert(msg)
                    }
                })
            }
        }
    }
</script>