import {ROLE_ENUMS} from '../../../conf/enums'
import pListAddDialog from './list-add-dialog'
import pListEditPasswordDialog from './list-edit-passwd-dialog'
import pListEditAuthDialog from './list-edit-auth-dialog'

export default {
    computed: {
        authEnums: () => ROLE_ENUMS
    },

    data() {
        return {
            loading: true,
            keyword: '',
            list: {
                items: [],
                page: 1,
                totalPage: 0,
                pageSize: 0
            }
        }
    },

    components: {
        pListAddDialog, pListEditPasswordDialog, pListEditAuthDialog
    },

    mounted() {
        this.refresh()
    },

    methods: {
        onKeywordChange() {
            this.refresh(this.list.page)
        },

        refresh(page = 1) {
            this.loading = true
            this.$store.dispatch('accountList', {
                data: {
                    page,
                    keyword: this.keyword
                },
                success: data => {
                    this.list = data
                    this.loading = false
                },
                error: msg => {
                    this.alert(msg)
                    this.loading = false
                }
            })
        },

        reset(row) {
            this.$refs['resetPasswdDialog'].showDialog({
                row,
                onSuccess: () => {
                    this.refresh(this.list.page)
                }
            })
        },

        remove(row) {
            this.confirm(`确认要删除${row.name}吗？`, () => {
                this.$store.dispatch('accountDelete', {
                    data: {
                        id: row.id
                    },
                    success: () => {
                        this.refresh(this.list.page)
                    },
                    error: msg => {
                        this.alert(msg)
                    }
                })
            })
        },

        onAddClick() {
            this.$refs['addDialog'].showDialog({
                onSuccess: () => {
                    this.refresh(this.list.page)
                }
            })
        },

        changeAuth(row) {
            this.$refs['editAuthDialog'].showDialog({
                onSuccess: () => {
                    this.refresh(this.list.page)
                },
                row: {
                    ...row,
                    auth: row.auth.split('').map(d => Number(d))
                }
            })
        }
    }
}