<style lang="less" src="./list.less"></style>

<template>
    <el-dialog title="添加管理员" :visible="open" width="800px" :before-close="onClose">
        <el-form :rules="rules" ref="form" :model="form" label-width="90px" size="small">
            <el-form-item label="用户名" prop="name">
                <el-input v-model="form.name" placeholder="名称只能是字母、数字、下划线，长度2 - 50位"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input v-model="form.password" placeholder="密码至少6位">
                    <template slot="append">
                        <el-button @click="randomPass">生成随机密码</el-button>
                    </template>
                </el-input>
            </el-form-item>
            <el-form-item label="开通权限">
                <el-checkbox-group v-model="auth">
                    <el-checkbox v-for="(v, k) in authEnums" :key="k" :label="v.value">{{v.text}}</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose">取 消</el-button>
            <el-button type="primary" @click="onSubmit">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
    import _ from 'lodash'
    import {ROLE_ENUMS} from '../../../conf/enums'

    export default {
        computed: {
            rules: () => ({
                name: [
                    {required: true, message: '请输入用户名', trigger: 'change'},
                    {
                        pattern: /^[a-zA-Z0-9_]+$/,
                        min: 2,
                        max: 50,
                        message: '只能是字母、数字、下划线，长度在 2 到 50 个字符',
                        trigger: 'change'
                    },
                ],
                password: [
                    {required: true, message: '请选择密码', trigger: 'change'},
                    {min: 6, message: '密码至少6位', trigger: 'change'}
                ]
            }),
            authEnums: () => ROLE_ENUMS
        },

        data() {
            return {
                open: false,
                form: {
                    name: '',
                    password: '',
                },
                callback: null,
                auth: []
            }
        },

        mounted() {

        },

        methods: {
            showDialog({onSuccess} = {}) {
                this.open = true
                this.form = {name: '', password: '', auth: {}}
                this.callback = onSuccess
            },

            onClose() {
                this.open = false
                this.$refs['form'].resetFields()
            },

            randomPass() {
                const str = '0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ'
                let ret = ''
                for (let i = 0; i < 20; i++) {
                    ret += str[Math.floor(Math.random() * str.length)]
                }
                this.form.password = ret
            },

            onSubmit() {
                this.$refs['form'].validate(valid => {
                    if (!valid) {
                        return
                    }
                    const auth = [...this.auth]
                    auth.sort()
                    this.$store.dispatch('accountCreate', {
                        data: {
                            ...this.form,
                            auth: auth.join('')
                        },
                        success: () => {
                            this.onClose()
                            if (_.isFunction(this.callback)) {
                                this.callback()
                            }
                        },
                        error: msg => {
                            this.alert(msg)
                        }
                    })
                })
            }
        }
    }
</script>